"use client";

import { useEffect, useState } from "react";
import { getAnimationSettings, updateAnimationSettings } from "@/lib/actions/site-settings";
import { AnimationSettings } from "@/lib/db/firestore-schema";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Switch } from "@/components/ui/switch";
import { Slider } from "@/components/ui/slider";
import { Label } from "@/components/ui/label";
import { toast } from "sonner";
import { Loader2 } from "lucide-react";
import { Separator } from "@/components/ui/separator";

export default function AppearanceSettingsPage() {
    const [settings, setSettings] = useState<AnimationSettings | null>(null);
    const [loading, setLoading] = useState(true);
    const [saving, setSaving] = useState(false);

    useEffect(() => {
        const fetchSettings = async () => {
            try {
                const data = await getAnimationSettings();
                setSettings(data);
            } catch (error) {
                toast.error("Failed to load settings");
            } finally {
                setLoading(false);
            }
        };
        fetchSettings();
    }, []);

    const handleSave = async () => {
        if (!settings) return;
        setSaving(true);
        try {
            const result = await updateAnimationSettings(settings);
            if (result.success) {
                toast.success("Settings saved successfully");
            } else {
                toast.error("Failed to save settings");
            }
        } catch (error) {
            toast.error("Something went wrong");
        } finally {
            setSaving(false);
        }
    };

    if (loading) {
        return <div className="p-8 flex justify-center"><Loader2 className="animate-spin h-8 w-8" /></div>;
    }

    if (!settings) return <div>Error loading settings.</div>;

    return (
        <div className="space-y-6 max-w-4xl">
            <div>
                <h2 className="text-3xl font-bold tracking-tight">Appearance Settings</h2>
                <p className="text-muted-foreground">
                    Customize the look and feel of your website's hero section.
                </p>
            </div>
            <Separator />

            <Card>
                <CardHeader>
                    <CardTitle>Futuristic Background</CardTitle>
                    <CardDescription>
                        Configure the animated background on the landing page.
                    </CardDescription>
                </CardHeader>
                <CardContent className="space-y-8">
                    <div className="flex items-center justify-between">
                        <div className="space-y-0.5">
                            <Label className="text-base">Enable Animation</Label>
                            <p className="text-sm text-muted-foreground">
                                Show the animated sound waves and notes on the hero section.
                            </p>
                        </div>
                        <Switch
                            checked={settings.enabled}
                            onCheckedChange={(checked) => setSettings({ ...settings, enabled: checked })}
                        />
                    </div>

                    <div className="space-y-4">
                        <div className="flex justify-between">
                            <Label>Animation Speed ({settings.speed.toFixed(1)}x)</Label>
                            <span className="text-xs text-muted-foreground">Slow — Fast</span>
                        </div>
                        <Slider
                            defaultValue={[settings.speed]}
                            min={0.1}
                            max={2.0}
                            step={0.1}
                            onValueChange={(val) => setSettings({ ...settings, speed: val[0] })}
                        />
                    </div>

                    <div className="space-y-4">
                        <div className="flex justify-between">
                            <Label>Note Density ({settings.density} notes)</Label>
                            <span className="text-xs text-muted-foreground">Few — Many</span>
                        </div>
                        <Slider
                            defaultValue={[settings.density]}
                            min={1}
                            max={50}
                            step={1}
                            onValueChange={(val) => setSettings({ ...settings, density: val[0] })}
                        />
                    </div>

                    <div className="space-y-4">
                        <div className="flex justify-between">
                            <Label>Glow Intensity ({Math.round(settings.glowIntensity * 100)}%)</Label>
                            <span className="text-xs text-muted-foreground">Dim — Bright</span>
                        </div>
                        <Slider
                            defaultValue={[settings.glowIntensity]}
                            min={0.1}
                            max={1.0}
                            step={0.1}
                            onValueChange={(val) => setSettings({ ...settings, glowIntensity: val[0] })}
                        />
                    </div>

                    <div className="pt-4 flex justify-end">
                        <Button onClick={handleSave} disabled={saving}>
                            {saving && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                            Save Changes
                        </Button>
                    </div>
                </CardContent>
            </Card>
        </div>
    );
}
